import React from 'react';
import { Link, animateScroll as scroll } from "react-scroll";
import About from '../About/About';
import Projects from '../Projects/Projects';
import MySkilz from '../MySkilz/MySkilz';
import './Main.css'

const Main = () => {
    function paralax(e) {
        let text = document.querySelector('.main__image')
        const x = (window.innerWidth - e.pageX) / 50
        const y = (window.innerWidth - e.pageY) / 50

        text.style.cssText = `background-position: ${x}% ${y}%`
        console.log(x);
    }

    return (
        <div className="main" >
            <div className="main__image" onMouseMove={paralax}>
                <div className="main__text-container">
                    <p className="main__text">
                        Привет, меня зовут Александр Смагин. Я начинающий фронтенд разрабботчик.
                    </p>
                    <Link
                        offset={-60}
                        className="main__link"
                        to="about"
                        activeClass="active"
                        smooth={true}
                        duration={700}>Посмотреть <span>➤</span>
                    </Link>
                </div>
            </div>
            <About />
            <Projects />
            <MySkilz />
        </div>
    )
}
export default Main;