import React from 'react';
import './Header.css';
import Menu from '../Menu/Menu';
import { Link, animateScroll as scroll } from "react-scroll";
const Header = ({ setIsScroll }) => {
    const [activeMenu, setActiveMenu] = React.useState(false)

    function onBurgerClick() {
        const burger = document.querySelector('.header__burger');
        burger.classList.toggle('header__burger_disable')
        if (burger.classList.contains('header__burger_disable')) {
            setActiveMenu(true)
        } else {
            setActiveMenu(false)
        }
    }
    function handleCloseMenu() {
        setActiveMenu(false)
    }

    return (
        <header className="header">
            <div class={`header__burger ${activeMenu ? 'header__burger_disable' : ''}`} onClick={onBurgerClick}>
                <span></span>
            </div>
            <Menu activeMenu={activeMenu} handleCloseMenu={handleCloseMenu} />
            <nav className="header__navigation">
                <div className='header__link-container'>
                    <Link
                        offset={-60}
                        to="skilz"
                        smooth={true}
                        duration={700}
                        activeClass={'active'}
                        spy={true}
                        className="header__text">Мои навыки</Link>
                    <span className='header__line'></span>
                </div>
                <div className='header__link-container'>
                    <Link
                        offset={-60}
                        to="projects"
                        smooth={true}
                        duration={700}
                        activeClass={'active'}
                        className="header__text"
                        spy={true}>Мои проекты</Link>
                    <span className='header__line'></span>
                </div>
                <div className='header__link-container'>
                    <Link
                        offset={-70}
                        to="about"
                        smooth={true}
                        duration={700}
                        activeClass={'active'}
                        className="header__text"
                        spy={true}>Обо мне</Link>
                    <span className='header__line'></span>
                </div>
            </nav>
        </header>)
}

export default Header;