import React from 'react';
import './Menu.css';
import { Link, animateScroll as scroll } from "react-scroll";
const Menu = ({ activeMenu, handleCloseMenu }) => {

    return (
        <div className={`menu ${activeMenu === true ? '' : 'open'}`}>
            <nav className={`menu__nav ${activeMenu === true ? 'open' : ''} `}>
                <div className='menu__link-container'>
                    <Link
                        offset={-50}
                        onClick={handleCloseMenu}
                        to="skilz"
                        smooth={true}
                        duration={700}
                        activeClass={'active'}
                        spy={true}
                        className="menu__text">Мои навыки</Link>
                    <span className='menu__line'></span>
                </div>
                <div className='menu__link-container'>
                    <Link
                        offset={-50}
                        onClick={handleCloseMenu}
                        to="projects"
                        smooth={true}
                        duration={700}
                        activeClass={'active'}
                        className="menu__text"
                        spy={true}>Мои проекты</Link>
                    <span className='menu__line'></span>
                </div>
                <div className='menu__link-container'>
                    <Link
                        offset={-50}
                        onClick={handleCloseMenu}
                        to="about"
                        smooth={true}
                        duration={700}
                        activeClass={'active'}
                        className="menu__text"
                        spy={true}>Обо мне</Link>
                    <span className='menu__line'></span>
                </div>
            </nav>
        </div>
    )
}

export default Menu;